.searchInput {
  width: 300px;
  height: 35px;
  border-radius: 6px;

  outline: none;
  border: 1px solid rgba(5, 5, 5, 0.5);
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.5);
  font-size: 20px;
  padding-left: 20px;
}
.FormSearch {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}
.searchButton {
  width: 120px;
  height: 35px;
  border-radius: 6px;
  outline: none;
  border: 1px solid rgba(5, 5, 5, 0.5);
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  background-color: #fff;
  font-size: 18px;
  font-weight: 500;
}
